.header_container{
    margin-top: 80px;
    font-family: 'Lora';
}

.header_titles{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #444;
}

.header_topTitle{
    position: absolute;
    top: 15%;
    font-size: 20px;
}

.header_bottomTitle{
    position: absolute;
    top: 15%;
    font-size: 100px;
}

.header_img{
    width: 100%;
    height: 450px;
    margin-top: 80px;
    object-fit: cover;
}